import { useState } from "react";
import { useDispatch } from "react-redux";
import {
  updateTask,
  updateTaskStatus,
  deleteTask,
} from "@/services/store/modules/taskSlice"; // Import actions

interface TaskCardProps {
  task: {
    _id: string;
    userId: string;
    title: string;
    description: string;
    dueDate: number; // Unix timestamp
    status: string; // "pending", "completed", "overdue"
  };
  onUpdate: (id: string, updates: Partial<any>) => void;
  onDelete: (id: string) => void;
}

const TaskCard = ({ task }: TaskCardProps) => {
  const dispatch = useDispatch();
  const [title, setTitle] = useState(task.title);
  const [description, setDescription] = useState(task.description);
  const [dueDate, setDueDate] = useState(
    new Date(task.dueDate).toISOString().split("T")[0]
  ); // Convert Unix timestamp to date string

  const handleStatusUpdate = () => {
    const newStatus =
      task.status === "pending"
        ? "completed"
        : task.status === "completed"
        ? "overdue"
        : "pending";
    dispatch(updateTaskStatus({ _id: task._id, status: newStatus }));
  };

  const handleUpdate = () => {
    dispatch(
      updateTask({
        _id: task._id,
        title,
        description,
        dueDate: new Date(dueDate).getTime(), // Convert date string back to Unix timestamp
        userId: task.userId,
        status: task.status,
      })
    );
  };

  const handleDelete = () => {
    dispatch(deleteTask(task._id)); // Dispatch delete action
  };

  return (
    <div className="w-full max-w-md p-6 space-y-6 bg-white rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300">
      <h3 className="text-2xl font-semibold text-center text-gray-800 mb-4">
        {title}
      </h3>

      <div className="space-y-4">
        <div>
          <label
            htmlFor="title"
            className="block text-sm font-medium text-gray-700"
          >
            Title
          </label>
          <input
            id="title"
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className="w-full px-4 py-2 mt-1 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            placeholder="Enter task title"
            required
          />
        </div>

        <div>
          <label
            htmlFor="description"
            className="block text-sm font-medium text-gray-700"
          >
            Description
          </label>
          <textarea
            id="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="w-full px-4 py-2 mt-1 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            placeholder="Enter task description"
            rows={4}
            required
          />
        </div>

        <div>
          <label
            htmlFor="dueDate"
            className="block text-sm font-medium text-gray-700"
          >
            Due Date
          </label>
          <input
            id="dueDate"
            type="date"
            value={dueDate}
            onChange={(e) => setDueDate(e.target.value)}
            className="w-full px-4 py-2 mt-1 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            required
          />
        </div>
      </div>

      <div className="flex justify-between mt-6">
        {/* Status Update Button */}
        <button
          onClick={handleStatusUpdate}
          className="px-4 py-2 bg-indigo-600 text-white rounded-lg shadow-sm hover:bg-indigo-500 focus:ring-2 focus:ring-indigo-300"
        >
          {task.status === "pending"
            ? "Mark as Completed"
            : task.status === "completed"
            ? "Move to Overdue"
            : "Move to Pending"}
        </button>

        {/* Update Task Button */}
        <button
          onClick={handleUpdate}
          className="px-4 py-2 bg-green-600 text-white rounded-lg shadow-sm hover:bg-green-500 focus:ring-2 focus:ring-green-300"
        >
          Update Task
        </button>

        {/* Delete Task Button */}
        <button
          onClick={handleDelete}
          className="px-4 py-2 bg-red-600 text-white rounded-lg shadow-sm hover:bg-red-500 focus:ring-2 focus:ring-red-300"
        >
          Delete Task
        </button>
      </div>
    </div>
  );
};

export default TaskCard;
